import React, { useEffect } from 'react';
import { Row, Col, Form } from 'antd';
import { BtnSave, BtnCancel } from 'components/common/button';
import { getSubjectGroups } from 'services/subjectGroup';
import { getFacultys } from 'services/faculty';
import SelectAjax from '../../../components/common/input/SelectAjax';
import { getStaffs } from '../../../services/teacher';
import { updateSupervisersTestOrganizing } from '../../../services/testOrganizing';

const TeacherExam = props => {
  useEffect(() => {
    if (props.visible) {
      props.form.setFieldsValue(props.recordEdit);
    }
  }, [props.visible]);

  const handleSubmit = e => {
    e.preventDefault();
    props.form.validateFields(async (err, values) => {
      if (err) {
        return;
      }
      await updateSupervisersTestOrganizing({ id: props.recordEdit.id, ...values });
      if (props.onSubmitSucess) {
        props.onSubmitSucess();
      }
    });
  };

  const { getFieldDecorator, getFieldValue, setFieldsValue } = props.form;
  const facultyId = getFieldValue('facultyId');
  const subjectGroupId = getFieldValue('subjectGroupId');
  return (
    <Form className="form-inline-label" layout="vertical" onSubmit={handleSubmit}>
      <Row gutter={24}>
        <Col span={12}>
          <Form.Item label="Khoa">
            {getFieldDecorator('facultyId', {})(
              <SelectAjax service={getFacultys} setFieldsValue={setFieldsValue} />
            )}
          </Form.Item>
        </Col>
        <Col span={12}>
          <Form.Item label="B mn">
            {
              getFieldDecorator('subjectGroupId', {
                // rules: [requiredRule],
              })
              (<SelectAjax service={getSubjectGroups} setFieldsValue={setFieldsValue} />)
            }
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Gim th 1">
            {getFieldDecorator('superviser1Id', { initialValue: '' })(
              <SelectAjax
                keyValue="staffId"
                keyLabel="fullname"
                service={getStaffs}
                params={{ facultyId, subjectGroupId }}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Gim th 2">
            {getFieldDecorator('superviser2Id', { initialValue: '' })(
              <SelectAjax
                keyValue="staffId"
                keyLabel="fullname"
                service={getStaffs}
                params={{ facultyId, subjectGroupId }}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={24}>
          <Form.Item label="Gim th 3">
            {getFieldDecorator('superviser3Id', { initialValue: '' })(
              <SelectAjax
                keyValue="staffId"
                keyLabel="fullname"
                service={getStaffs}
                params={{ facultyId, subjectGroupId }}
              />
            )}
          </Form.Item>
        </Col>
        <Col span={24} style={{ textAlign: 'right' }}>
          <div className="btn-group">
            <BtnCancel onClick={() => props.onCancel()} />
            <BtnSave htmlType="submit" />
          </div>
        </Col>
      </Row>
    </Form>
  );
};

export default Form.create({ name: 'TeacherExam' })(TeacherExam);



